
import { useDataStore } from "@/store/main";
import {
  defineComponent,
  computed,
  onBeforeMount,
  ref,
  onMounted
} from "vue";
import { useRoute, useRouter } from "vue-router";
import Cursor from "@/components/CustomCursor.vue";
import Project from "@/types/project";
import gsap from 'gsap'
import { ScrollTrigger } from "gsap/ScrollTrigger";

export default defineComponent({
  components: {
    Cursor
  },
  setup() {
    const store = useDataStore();
    const route = useRoute();
    const router = useRouter()
    const gallery = ref<string[]>([])

    const scrollToTop = () => {
      window.scrollTo({
        top: 50,
        behavior: "smooth",
      });
    };
    onBeforeMount(() => {
      store.getDataFromFirebase();
      router.beforeEach((to, from, next) => {
        scrollToTop();
        next();
      });
    })


    const projectId = computed(() => {
      return Array.isArray(route.params.id)
        ? parseInt(route.params.id[0])
        : parseInt(route.params.id);
    })
    const currentProject = computed(() => {
      return store.projects.find(project => project.id === projectId.value) as Project
    })



    // Gsap animations 
    onMounted(() => {
      gsap.registerPlugin(ScrollTrigger);
      const tl = gsap.timeline();
      gallery.value = gsap.utils.toArray(".gallery-container-img");
      gallery.value.forEach((el) => {
        tl.to(el, {
          opacity: 1,
          duration: 5,
          transform: "translateX(0)",
          scrollTrigger: {
            trigger: el,
            start: "center center",
            end: "+=800",
            scrub: true,
          },
        });
      });
    })
    return {
      store, route,
      currentProject,
    };
  },
});
